.row{
    color: white;
    margin-left: 20px;
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row__poster{
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    transition: transform 450ms;

}
.row__posters::-webkit-scrollbar{
    display: none;
   
}
.row__poster:hover{
 transform: scale(1.08);
 opacity: 1;   
}